@import "../../sass/variables";
@import "../../sass/typography";

.work {
  padding: 10vh 0;
  &__title {
    display: inline-block;
    margin: 0;
    border-bottom: 7px solid $primary;
  }
  .small-card-container {
    display: grid;
    grid-template-areas: ". .";
    gap: 30px;
  }
}
